import React from "react";
import classNames from "classnames";
import {
    useHistory
  } from "react-router-dom";
import { useApp } from './AppContext';

const TopButton = ({phase, linkTo}) => {
    const history = useHistory();
    const {isCurrPhase, isEnabled} = useApp(); 
    const clickHandler = () => { 
        history.push(linkTo);
    }
    return (
        <li className={classNames("phaseTabs", {})}>
            <button 
                className={classNames("btn", "btn-primary", {"btn-disabled":!isEnabled(phase), "currPhase":isCurrPhase(phase)} )} 
                onClick={clickHandler}>{phase}
            </button>
        </li>
    );
} 

export default TopButton; 