import React, { useState, useEffect } from "react";
import classNames from "classnames";
import {
  HashRouter as Router,
  Switch,
  Route,
  Link,
  Redirect
} from "react-router-dom";
import { AppContext } from "./AppContext";
import { CardText } from "./CardText.js"
import CarouselCards from "./CarouselCards.js"
import NextArrow from "./NextArrow";
import PrevArrow from "./PrevArrow";
import { TwoDivOutline } from "./TwoDivOutline.js"
import TopButton from "./TopButton";
import { Tables } from "./Tables";
import { STEP_DATA } from "../data/m2ca.js"

const INITIAL_STATE={
  "D": false, 
  "E": false, 
  "B": false, 
  "R": false, 
  "IE": false, 
  "F": false      
}
const COMPLETION_STATUS={
  "D": false, 
  "E": false, 
  "B": false, 
  "R": false, 
  "IE": false, 
  "F": false      
}
const NEXT_LINKS={
  "D": "/explain", 
  "E": "/benchmarks", 
  "B": "/review", 
  "R": "/identifyAndExamine", 
  "IE": "/formalize", 
  "F": "/formalize"      
}
const PREV_LINKS={
  "D": "/define", 
  "E": "/define", 
  "B": "/explain", 
  "R": "/benchmarks", 
  "IE": "/review", 
  "F": "/identifyAndExamine"      
}
export const MainOutline = () => {
    const [currPhase, setCurrPhase] = useState(null);
    const [phaseEnabled, setPhaseEnabled] = useState(INITIAL_STATE);
    const [phaseCompleted, setPhaseCompleted] = useState(COMPLETION_STATUS); 
    function enablePhase(phaseID){ setPhaseEnabled(prevState=> ( { ...prevState, [phaseID]:true } ))}
    function isCurrPhase(phaseID) { 
      return currPhase===phaseID;
    }
    function isEnabled(phaseID){ 
      return phaseEnabled[phaseID];
    }
    function isCompleted(phaseID) { 
      return phaseCompleted[phaseID];
    }
    function completePhase(phaseID) { 
      setPhaseCompleted(prevState=> ( { ...prevState, [phaseID]:true } ))
    }
    useEffect( 
      () => {
        enablePhase("D");
        setCurrPhase("D");
      }, []
    )
    return (
        <Router>
          <AppContext.Provider
              value={{
                enablePhase: enablePhase,
                currPhase: currPhase, 
                setCurrPhase: setCurrPhase, 
                isCurrPhase: isCurrPhase, 
                isEnabled: isEnabled, 
                isCompleted: isCompleted, 
                completePhase: completePhase, 
                stepData: STEP_DATA, 
                next_links: NEXT_LINKS, 
                prev_links: PREV_LINKS
              }}
          >
          <div className="outline-container">
            <nav>
             <div>
                <ul>
                    <TopButton phase="D" linkTo="/define"/>
                    <TopButton phase="E" linkTo="/explain"/>
                    <TopButton phase="B" linkTo="/benchmarks"/>
                    <TopButton phase="R" linkTo="/review"/>
                    <TopButton phase="IE" linkTo="/identifyAndExamine"/>
                    <TopButton phase="F" linkTo="/formalize"/>
                </ul>
              </div>
            </nav>
            
            <Switch>
              <Route
                exact path="/"
                render={ () => { return ( <Redirect to="/define" /> )} }
              />
              <Route path="/define">
                <CardText phase="D" nextPhase="E"/>
              </Route>
              <Route path="/explain">
                <CardText phase="E" nextPhase="B"/>
              </Route>
              <Route path="/benchmarks">
                <TwoDivOutline phase="B" nextPhase="R"/>
              </Route>
              <Route path="/review">
                <CarouselCards nextPhase="IE"/>
              </Route>
              <Route path="/identifyAndExamine">
                <Tables phase="IE" nextPhase="F"/>
              </Route>
              <Route path="/formalize">
                <CardText phase="F" />
              </Route>
            </Switch>
            {/* { currPhase!=null &&
              <div className="arrows">
                <Link className={classNames({"link-disabled":currPhase==="D"})} to={prevLink}><button className={classNames("nav-btns", {"btn-disabled":currPhase==="D"})}><PrevArrow/>BACK</button></Link>
                <Link className={classNames({"link-disabled":!isCompleted(currPhase)})} to={nextLink}><button className={classNames("nav-btns",  {"btn-disabled":!isCompleted(currPhase)})}>NEXT<NextArrow/></button></Link>
              </div>
            } */}
          </div>
          </AppContext.Provider>
        </Router>
      );
  };