import React, {useEffect} from "react";
import { useApp } from "./AppContext";
import { Form } from "./Form";
import classNames from "classnames";
import PrevArrow from "./PrevArrow";
import NextArrow from "./NextArrow";
import { useHistory } from 'react-router';



export function CardText({phase}) {
    const history = useHistory(); 

    const {setCurrPhase, enablePhase, completePhase, stepData, next_links, prev_links} = useApp();

    const phaseData = stepData.find(e => e.step === phase)

    useEffect(() => {
        setCurrPhase(phase); 
    })

    useEffect(() => {
        enablePhase(phase);
        completePhase(phase); 
    }, [phase])
    
    function next() { 
        history.push(next_links[phase]);
    }

    function prev() { 
        history.push(prev_links[phase]);
    }
    return (
        <>
            <p>
                <b>{ phaseData.title.charAt(0) }</b>
                { phaseData.title.substring(1, phaseData.length) }
            </p>
            <div className="card">
                <div className="card-body">
                    <h5 className="card-title"><b>{phaseData.subtitle}</b></h5>
                    <p className="card-text">{phaseData.description}</p>
                    <ol>
                        { phase==="E" && 
                            phaseData.substeps.map((data) => (
                            <li key={data}>{data}</li>
                            ))
                        }
                        { phase==="F" && 
                            <Form />
                        }
                    </ol>
                </div>
            </div>
            <div className="arrows">
                <button onClick={prev} className={classNames({"btn-disabled":phase==="D"}, "nav-btns")}><PrevArrow/>PREV</button>
                <button onClick={next} className={classNames({"btn-disabled":phase==="F"}, "nav-btns")}>NEXT<NextArrow/></button>
            </div>
        </>
    );
}

export default CardText;