
import classNames from "classnames";
import { useApp } from './AppContext';

const Slide = ({jsonData, title, subtitle, body, show=false}) => {
    const {stepData} = useApp(); 
    let slideshowData = stepData[3].slideshowData;
    return(
        <div className={classNames("slide", !show && "hidden")}>
            {jsonData=="slideshow" && <h5 id="slideTitle" className={classNames("card-title","pageCount")}><b>{title+1} of {Object.keys(slideshowData).length}</b></h5>}
            <h5 className={classNames("card-title")}>{subtitle}</h5>
            {jsonData=="slideshow" && <p id="slideText" className="card-text">{body}</p> }
            {jsonData=="benchmark" && <div id="slideText" className="card-text"><ol>{body.map((data, index) => (<li key={index}>{data}</li>))}</ol></div>}
        </div> 
    ); 
}
export default Slide; 
