import React, {useEffect} from "react";
import classNames from "classnames";
import { useApp } from "./AppContext";
import { useHistory } from "react-router";
import PrevArrow from "./PrevArrow";
import NextArrow from "./NextArrow";



export const Tables = ({phase, nextPhase}) => {
  const history = useHistory(); 

  const {setCurrPhase, enablePhase, completePhase, stepData} = useApp();

    useEffect(
      () => {
          setCurrPhase(phase);
          enablePhase(phase);
          completePhase(phase); 
      }, []
    ) 

    function next() { 
      history.push("/formalize");
    }

    function prev() { 
      history.push("/review");
    }

    return (
      <>
        <p><b>I</b>dentify and <b>E</b>xplain:</p>
        <div className="table-container">
        <div id="slideshowCard" className="card">
                <div className="float-container">
                    <div className="card-body, slides, float-child">
                    <div className={classNames("slide")}>
                      <h5 id="slideTitle" className={classNames("card-title")}>Needs implemented</h5>
                      <p id="slideText" className="card-text">Needs implemented</p>
                  </div> 
                    </div> 
                </div>
          </div>
           {/* Step: {stepData[4].tableData.table0[0].step} */}
        </div>
        <div className="arrows">
            <button onClick={prev} className={classNames("nav-btns")}><PrevArrow/>PREV</button>
            <button onClick={next} className={classNames("nav-btns")}>NEXT<NextArrow/></button>
        </div>
      </>
    );
  };