import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router';
import { useApp } from './AppContext';
import Slide from "./Slide"; 
import NextArrow from './NextArrow';
import PrevArrow from './PrevArrow';
import classNames from 'classnames';

export function CarouselCards(props) {
    const history = useHistory(); 
    const [curSlide, setCurSlide] = useState(0); 
    const {setCurrPhase, enablePhase, completePhase, stepData} = useApp();
    useEffect(
        () => {
            setCurrPhase("R");
            enablePhase("R");
        }, []
    ) 
    const slideshowData = stepData[3].slideshowData;
    function isLastSlide() { 
        return curSlide===slideshowData.length-1;
    }
    function prev() { 
        if(curSlide===0) { 

            history.push('/benchmarks'); 
        } else { 
            setCurSlide(curSlide-1);
        }
    }
    function next() { 
        if(isLastSlide()){ 
            history.push('/identifyAndExamine'); 
                    
        } else { 
            setCurSlide(curSlide+1);
            if(curSlide===slideshowData.length-2){
                completePhase("R");
            }
        }
    }
    return (
        <>
            <p><b>R</b>eview what was supposed to happen:</p>
            <div id="slideshowCard" className="card">
                <div className="float-container">
                    <div className="card-body, slides, float-child">
                        {slideshowData.map((data, index) => (
                            <Slide jsonData={"slideshow"} key={index} title={index} body={data.body} show={index===curSlide}/>
                        ))}
                    </div>
                </div>
            </div>
            <div className="arrows">
                <button onClick={prev} className={classNames("nav-btns")}><PrevArrow/>PREV</button>
                <button onClick={next} className={classNames("nav-btns")}>NEXT<NextArrow/></button>
            </div>
        </>
    );
}

export default CarouselCards;
