import React, {useEffect, useState} from "react";
import { useApp } from "./AppContext";
import classNames from "classnames";
import Slide from "./Slide";
import PrevArrow from "./PrevArrow";
import NextArrow from "./NextArrow";
import { useHistory } from 'react-router';


let clickedBefore = []; 
let maxSlide = 0; 

export function TwoDivOutline(props) {
    const history = useHistory(); 
    const {stepData} = useApp(); 
    const benchmarkInfo = stepData[2].benchmarkInfo;

    benchmarkInfo.map((data, index) => (clickedBefore[index] = false))

    const [curSlide, setCurSlide] = useState(0); 
    useEffect(
        () => {
            setCurrPhase("R");
        }, []
    ) 
    function isLastSlide() { 
        return curSlide===benchmarkInfo.length-1;
    }
    function prev() { 
        if(curSlide===0) { 
            history.push('/explain')
        } else { 
            setCurSlide(curSlide-1);
        }
    }
    function next() { 
        if(isLastSlide()){    
            history.push('/review');                  
        } else { 
            setCurSlide(curSlide+1);
            if(curSlide===benchmarkInfo.length-2){
                completePhase("B");
            }
        }
        for(var i = 0; i<clickedBefore.length; i++){ 
            if(i == curSlide) { 
                clickedBefore[curSlide+1] = true; 
            }
        }
    }
    function clickedStatus(n) { 
        if (curSlide>maxSlide) { 
            maxSlide = curSlide; 
        }
        if(n==curSlide || n<=maxSlide) { 
            clickedBefore[n] = true;
        }
        return clickedBefore[n];
    }
    const {setCurrPhase, enablePhase, completePhase} = useApp();
    useEffect(
        () => {
            setCurrPhase("B");
            enablePhase("B");
        }, []
    ) 
    clickedBefore[0] = true; 
    function setClicked(n){ 
        if (curSlide>maxSlide) { 
            curSlide = n; 
        }
        setCurSlide(n); 
    }
    return (
        <>
            <p>Performance <b>B</b>enchmarks:</p>
            <div className="container">
                <div className="card-group">
                    <div className="card">
                        <div className="card-block">
                            <div className="card-body">
                                <div className="card-title">Below are management actions you were supposed to perform in this scenario:</div>
                                <div className="card-text">
                                    <ol>{ benchmarkInfo.map((data, index) => 
                                        (<li className={classNames({"disabled":!clickedStatus(index)})} key={index} onClick={() => setClicked(index)} style={{"color": "black"}}><u id={index} className={classNames("pointer", {"disabled":!clickedStatus(index)})}>{data.action}</u></li>))}
                                    </ol>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div id="slideshowCard" className="card" style={{"flexGrow":"2"}}>
                    <div>
                            <div className="card-body, slides, float-child">
                                {benchmarkInfo.map((data, index) => (
                                    <Slide jsonData={"benchmark"} key={index} title={index} subtitle={data.action} body={data.management} show={index===curSlide}/>
                                ))}
                            </div>
                        </div>
                        
                    </div>
                </div>
            </div>
            <div className="arrows">
                <button onClick={prev} className={classNames("nav-btns")}><PrevArrow/>PREV</button>
                <button onClick={next} className={classNames("nav-btns")}>NEXT<NextArrow/></button>
            </div>
        </>
    );
}

export default TwoDivOutline;